/* ======= inputmask start ======= */
const form = document.querySelectorAll('.form'),
  telSelector = document.querySelectorAll('input[type="tel"]'),
  inputMask = new Inputmask('+7 (999) 999-99-99')

inputMask.mask(telSelector)

// telSelector.forEach((e) => {
//   console.log(e.inputmask.undoValue);
// });

/* ======= inputmask end ======= */
/* ======= validateform start ======= */

import JustValidate from 'just-validate'

form.forEach((e) => {
  // console.log(e)
  const validate = new JustValidate(e)

  validate
    .addField('.email', [
      {
        rule: 'required',
        errorMessage: 'Заполните поле Email',
      },
      {
        rule: 'email',
        errorMessage: 'Введите правильный',
      },
    ])

    .addField('.name', [
      {
        rule: 'minLength',
        value: 3,
        errorMessage: 'Не должно быть меньше 3х символов',
      },
      {
        rule: 'maxLength',
        value: 10,
        errorMessage: 'Не болееее 10 символов',
      },
    ])

    .addField('.tel', [
      {
        required: true,
        rule: 'maxNumber',
        value: 10,
        errorMessage: 'Введите телефон',
      },
    ])
})

